import React from 'react';
import i18n from '@dhis2/d2-i18n';
import { ReactFinalForm, CheckboxFieldFF } from '@dhis2/ui';
import { FormField } from '../index';
const {
  Field
} = ReactFinalForm;
const NAME = 'includeChildren';
const DATATEST = 'input-include-children';
const SHORT_LABEL = i18n.t('Include first level units');
const LABEL = i18n.t('Include the first level of organisation units inside selections');

const IncludeChildren = () => <FormField label={SHORT_LABEL} dataTest={DATATEST}>
        <Field type="checkbox" component={CheckboxFieldFF} name={NAME} label={LABEL} dataTest={`${DATATEST}-sf`} />
    </FormField>;

export { IncludeChildren };