import { FORM_ERROR } from '../../utils/final-form';
import { uploadFile } from '../../utils/helper';

const onImport = ({
  baseUrl,
  setProgress,
  addTask,
  setShowFullSummaryTask
}) => async values => {
  const {
    dryRun,
    files
  } = values; // send xhr

  const apiBaseUrl = `${baseUrl}/api/`;
  const endpoint = 'metadata/gml.json';
  const params = [`dryRun=${dryRun}`, 'format=json'].join('&');
  const url = `${apiBaseUrl}${endpoint}?${params}`;

  try {
    await uploadFile({
      url,
      file: files[0],
      format: 'gml',
      type: 'GML_IMPORT',
      setProgress,
      addEntry: (id, entry) => addTask('gml', id, { ...entry,
        jobDetails: values
      })
    });
  } catch (e) {
    const errors = [e];
    return {
      [FORM_ERROR]: errors
    };
  } finally {
    setShowFullSummaryTask(true);
  }
};

export { onImport };